.content {
    column-gap: normal;
    background-color: whitesmoke;

}

.messages {
color: black;
padding: 10px;

}

.messages .message {
color: black;

}

.dialogs.messages {
    color: black;
}

.errorMessage {
    color: red ;
    font-family:'Times New Roman', Times, serif;
}

.fieldNewMessageBody {
    float: left; 
    resize: none; 
    word-wrap: break-word;
    font-size: large; 
    white-space: pre-wrap;
    font-family: 'Times New Roman', Times, serif;
    font-size: 110%;
}

button {
    font-family: 'Times New Roman', Times, serif;
    font-size: 100%;
    width: 60px;
    height: 30px;
    display: block;
    color: #000000;
    text-align: center;
    border-radius: 5px;
    font-family:'Times New Roman', Times, serif;
    font-weight: 300;
    background: transparent;
    cursor: pointer;
    position:relative; 
    display: inline-block;
    box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
     7px 7px 20px 0px rgba(0, 0, 0, 0.1),
     4px 4px 5px 0px rgba(0,0,0,.1);
    outline: none;
    margin-left: 15px;
   
}

input {
    display: block;
    width: 400px;
    padding: 6px 12px; 
    font-size: 14px;
    font-family: 'Times New Roman', Times, serif;
    line-height: 1.42857143;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 5px; 

}
